main section.background-img{height:70vh;background-image:url(../../assets/images/日本櫻花.jpg);background-size:cover;background-position:center;transition:all .75s ease;position:relative;z-index:0;display:flex;justify-content:center;align-items:center}main section.background-img div.filter{width:100%;height:100%;background-color:rgba(0,0,0,.5);position:absolute;z-index:-1}main section.background-img h3{color:#fff;font-size:2.5rem;text-align:center}main section.info-circles{background-color:#fff;padding:2rem 1rem}main section.info-circles h3{text-align:center;font-size:2rem}main section.info-circles div.circles{display:flex;flex-wrap:wrap;justify-content:center}main section.info-circles div.circles div.circle{flex:0 1 250px;display:flex;flex-direction:column;align-items:center;padding:1rem}main section.info-circles div.circles div.circle img{width:80%;height:100%;border-radius:50%;padding:.3rem}main section.info-circles div.circles div.circle img:hover{border:3px solid rgba(152,57,255,.5)}main section.empty-container{height:70vh;display:flex;align-items:center;justify-content:center;background-color:rgba(0,0,0,.7)}main section.empty-container h3{font-size:2.5rem;color:#fff}main section.google-map{background-color:#fff;padding:2rem 1rem;text-align:center}main section.google-map h3{font-size:2.5rem}main section.google-map p{font-size:1.25rem;margin:1rem}main section.google-map iframe{width:100%;height:40vh}